.section-dudas {
  background-image: url("../images/olas.png");
  background-position: top;
  background-size: contain;
  background-repeat: no-repeat;
  padding-bottom: 2rem;
  padding-top: 8rem; }
  @media (max-width: 767.98px) {
    .section-dudas {
      padding-top: 4rem; } }

.section-team .img-team {
  padding: 1rem; }

.section-team .slick-next:before,
.section-team .slick-prev:before {
  display: none; }

.section-team p.title-team {
  color: #808080;
  font-weight: 400;
  font-family: Roboto; }
  .section-team p.title-team span {
    font-weight: 800;
    color: #2f4b91; }

.section-team .inf-team {
  background-image: linear-gradient(to bottom, #ffffff, #f4f6fe, #e4effd, #d0e9fb, #b9e4f5);
  padding: 2rem;
  border: 1px solid #e6e6e6;
  border-radius: 15px; }
  .section-team .inf-team li {
    color: #808080; }
    .section-team .inf-team li span {
      font-weight: 800; }

.section-team .slider-for {
  position: relative; }
  .section-team .slider-for:before {
    content: "";
    display: block;
    width: 0;
    height: 0;
    border-top: 25px solid transparent;
    border-bottom: 25px solid #e6e6e6;
    border-right: 25px solid transparent;
    position: absolute;
    top: -50px;
    left: 8.5rem;
    border-left: 25px solid transparent; }
    @media (max-width: 1399.98px) {
      .section-team .slider-for:before {
        left: 7.3rem; } }
    @media (max-width: 1199.98px) {
      .section-team .slider-for:before {
        left: 5.8rem; } }
    @media (max-width: 991.98px) {
      .section-team .slider-for:before {
        left: 4rem; } }
    @media (max-width: 767.98px) {
      .section-team .slider-for:before {
        left: 50%;
        transform: translateX(-50%); } }

.section-team ul {
  padding-left: 0; }
  .section-team ul li {
    color: #808080;
    list-style: none; }
    .section-team ul li span {
      font-weight: 800; }
    .section-team ul li a {
      color: #808080;
      text-decoration: none;
      word-break: break-all;
      font-weight: 800;
      transition: all 0.3s; }
      .section-team ul li a:hover {
        color: #2f4b91; }
